.cell-style{
    width: min(25px, 6vw);
    height: min(25px, 6vw);
    border: 0.5px solid black;
    text-align: center;
    margin: 0;
    padding: 0;
}

.container-style{
   display: flex;
   justify-content: center;
   margin-top: 1%;
   padding: 0 5px;
   overflow-x: auto;
   max-width: 100vw;
}

.source-node{
    background-color: green;
}

.destination-node{
    background-color: red;
}

.wall-node{
    background-color: black;
}

.path-node{
    background-color: hotpink;
}

.visited-node {
    background: rgba(0, 190, 218, 0.75);
    animation-name: visitedAnimation;
    animation-duration: 1s;
}
  
@keyframes visitedAnimation {
    0% {
      transform: scale(0.3);
      background-color: rgba(0, 0, 66, 0.75);
      border-radius: 100%;
    }
  
    50% {
      background-color: rgba(17, 104, 217, 0.75);
    }
    75% {
      transform: scale(1.2);
      background-color: rgba(0, 217, 159, 0.75);
    }
    100% {
      transform: scale(1);
      background-color: rgba(0, 190, 218, 0.75);
    }
  }  
  
  .path-node {
    background: rgba(235, 11, 235, 0.75);
    animation-name: pathAnimation;
    animation-duration: 1s;
  }
  @keyframes pathAnimation {
    0% {
      transform: scale(0.3);
      background-color: rgba(138, 8, 245, 0.75);
      border-radius: 100%;
    }
  
    50% {
      background-color: rgba(228, 17, 217, 0.75);
    }
    75% {
      transform: scale(1.2);
      background-color: rgba(248, 14, 248, 0.75);
    }
    100% {
      transform: scale(1);
      background-color: rgba(0, 4, 255, 0.75);
    }
  }

@media (max-width: 768px) {
    .cell-style {
        width: min(20px, 5vw);
        height: min(20px, 5vw);
    }
    
    .container-style {
        margin-top: 2%;
        padding: 0 2px;
    }
}

@media (max-width: 480px) {
    .cell-style {
        width: min(15px, 4vw);
        height: min(15px, 4vw);
    }
    
    .container-style {
        margin-top: 3%;
        padding: 0 1px;
    }
}